import { Component } from '@angular/core'; 
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { AngularFirestore } from '@angular/fire/firestore';
import { Pipe, PipeTransform } from '@angular/core';
@Component({ 
selector: 'login-app', 
templateUrl: './login.component.html', 
styleUrls: [ './login.component.css' ] 
}) 


export class LoginComponent { 
 
    name : string;
    loginForm : FormGroup;
    alreadyDone : boolean = false;
    users : any ;
constructor(private router: Router,public db: AngularFirestore) { } 

 

ngOnInit() { 

    this.loginForm = new FormGroup({
        'name': new FormControl(this.name, [
          Validators.required
        ])
    });
} 


onSubmit(){
    this.alreadyDone = false;

    this.db.collection('User')
      .valueChanges()
      .subscribe(users => {
          this.users = users;
        //let isUserExists =  users.filter(user => user.Name === this.loginForm.get('name').value);
        let isUserExists = this.users.filter(user => user.Name === this.loginForm.get('name').value);
        if(isUserExists.length){
            this.alreadyDone = true;
        }else{
            var name = this.loginForm.get('name').value;
            this.router.navigate(['/survey',name]);
        }
      });

      /*this.db.collection('User').add({
        Id: 1,
        Name: "Mohan",
        Survey : ""
      });*/  

}
} 
 