import { Component } from '@angular/core'; 
import { AngularFirestore } from '@angular/fire/firestore';
import { ActivatedRoute,Router } from '@angular/router';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
 
@Component({ 
selector: 'survey-app', 
templateUrl: './survey.component.html', 
styleUrls: [ './survey.component.css' ] 
}) 


export class SurveyComponent { 

    survey : any;
    screenToShowlist :any = [];
    isNext : string = "Next";
    isPrev : boolean = false;
    name : string;
    lastScreenNo : number;
    isFromLoad : boolean = false;
    userList : any;
    progressStyle : any = {"width" : "0%"};
    completedScreen : number = 0;
    questions : any ;
    selectedUser : any;
    docId : string;
    ques : any;
    screenToShow : any={};
    constructor(public db: AngularFirestore,public route: ActivatedRoute,private router: Router) {
        this.isFromLoad = true;
    } 

    ngOnInit() {
        this.route.params.subscribe(params => {
            this.name = params['name'];
          });

          //let userCollection = this.db.collection<User>('User');
       /*   this.db.collection('User', ref => ref.where('Name', '==', this.name)).snapshotChanges().pipe(map(actions => {
            return actions.map(a => {
              const data = a.payload.doc.data();
              const id = a.payload.doc.id;
              return { id, ...data };
            });
          })).subscribe(doc => {
               if(doc.length){

               }else{
                this.db.collection('ScreenQuestions').valueChanges().subscribe(questions => { 
                    this.questions = questions;
                    this.survey = JSON.parse(this.questions[0].Question);
                    this.lastScreenNo = 1;
                    this.screenToShowlist = this.survey.filter(screen => screen.ScreenNumber === this.lastScreenNo);
                    this.isPrev = true;
                });
               }

            
            });*/
         
        
        this.db.collection('User', ref => ref.where('Name', '==', this.name)).valueChanges().
                subscribe(selectedUser =>{
                    this.selectedUser = selectedUser;
                    if(this.isFromLoad){
                        
                    if(this.selectedUser.length){

                        this.selectedUser.forEach(user => {
                            user.SubmittedDate = new Date(user.SubmittedDate);
                        });
                        
                        this.selectedUser.sort(function(a, b){
                            return b.SubmittedDate -a.SubmittedDate; //new Date(b.SubmittedDate) - new Date(a.SubmittedDate)
                        });
 
                        this.survey = JSON.parse(this.selectedUser[0].Survey);
                        this.screenToShow = this.survey[0];
                       // this.lastScreenNo = 1;
                        
                       // this.screenToShowlist = this.survey.filter(screen => screen.ScreenNumber === this.lastScreenNo);
                        this.isPrev = true;
                        this.progressStyle= {"width" : "100%"};
                    }else{
                        this.db.collection('ScreenQuestions').valueChanges().subscribe(questions => { 
                        this.ques = questions;
                        this.survey = JSON.parse(this.ques[0].Question);
                        this.screenToShow = this.survey[0];
                        this.isPrev = true;
              });
                     }
                    }  
                }) 
        
    } 
 
    onNext(){
        let screenNumber = this.screenToShow.ScreenNumber;
        let lengthOfScreens =  this.survey.length;
        this.progressStyle.width = (100 / this.survey.length+1)*(screenNumber+1)+"%";
        if(screenNumber === lengthOfScreens-1){

        this.db.collection('User').add({
                            Name: this.name,
							Survey : JSON.stringify(this.survey),
							SubmittedDate : new Date().toString(),
							Status : "Active"
                        });

         this.router.navigateByUrl('/surveyComplete');

        return;
        }

        this.screenToShow = this.survey[this.screenToShow.ScreenNumber + 1];
        if(this.screenToShow.ScreenNumber === lengthOfScreens-1){
                this.isNext = "Submit";
        }
        this.isPrev = this.screenToShow.ScreenNumber > 0 ? false : true;
    }
    onPrevious(){
        this.isNext = "Next";
        this.lastScreenNo = this.screenToShow.ScreenNumber - 1;
        this.screenToShow = this.survey.filter(screen => screen.ScreenNumber === this.lastScreenNo)[0];
        if(this.lastScreenNo <=  0 ){
            this.isPrev =true;
        }
    }

} 
 

export class User{
    Name: string;
    Survey : string;
    SubmittedDate : string;
    Status : string;
}