import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule,ReactiveFormsModule } from '@angular/forms';
import { RouterModule,Routes } from '@angular/router';
import { AngularFirestoreModule } from '@angular/fire/firestore';
import { AppComponent } from './app.component'; 
import { LoginComponent } from './loginComponent/login.component';
import { SurveyComponent } from './surveyComponent/survey.component';
import { AdminComponent } from './adminComponent/admin.component';
import { DashboardComponent } from './adminComponent/dashboardComponent/dashboard.component';
import { AddusersComponent } from './adminComponent/addusersComponenet/addusers.component';
import { QuestionuploadComponent } from './adminComponent/questionUploadComponent/questionUpload.component';
import { SettingComponent } from './adminComponent/settingComponent/setting.component';
import { environment } from '../environments/enviroments';
import { AngularFireModule } from '@angular/fire';
import { AngularFirestore } from '@angular/fire/firestore';
import { SurveyCompleteComponent } from './surveyCompleteComponent/surveyComplete.component';
const routes: Routes = [
  { path: '', redirectTo: '/login', pathMatch: 'full' },
  { path: 'login', component: LoginComponent },
  { path: 'survey/:name', component: SurveyComponent },
  { path: 'surveyComplete', component: SurveyCompleteComponent },
  { path: 'admin', component: AdminComponent,children: [
      { path: 'dashboard',component: DashboardComponent },
      { path: 'uploadquestion', component: QuestionuploadComponent},
      { path: 'adduser', component: AddusersComponent},
      { path: 'setting', component: SettingComponent},
      { path: '', redirectTo: '/admin/dashboard', pathMatch: 'full'}]
   }
];

@NgModule({
  imports:      [ BrowserModule, FormsModule, RouterModule.forRoot(routes),ReactiveFormsModule,
                  AngularFireModule.initializeApp(environment.firebase),
    AngularFirestoreModule],
  declarations: [ AppComponent,LoginComponent,SurveyComponent,SurveyCompleteComponent,AdminComponent,
                  DashboardComponent,QuestionuploadComponent,AddusersComponent,
                  SettingComponent],
  bootstrap:    [ AppComponent ],
  providers: [AngularFirestore],

})
export class AppModule { 
  
}


